import React from 'react';
import { Wrapper } from './styles';
import { useState } from 'react';
import Empty from '../../../Empty';
import RegisteredOpinionItem from './RegisteredOpinionItem';
import img1 from '../../../../Assets/images/1.jpg';
import ProfileSubmitOpinionModal from '../../Modals/ProfileSubmitOpinionModal';
import { useSelector } from 'react-redux';
import DeleteOpinionModal from '../../Modals/DeleteOpinionModal';

const RegisteredOpinions = () => {

    const {
        showEditOpinionModal,
        showDeleteOpinionModal
    } = useSelector((state) => state.Orders)

    const [opinions, setOpinions] = useState([
        {
            id: 0,
            product: {
                id: 0,
                title: "              ",
                imgSrc: img1
            },
            opinionTitle: " ",
            image: img1,
            opinionText: "                  ",
            score: 1,
            status: " ",
            suggestion: "like"
        },
        {
            id: 1,
            product: {
                id: 0,
                title: "              ",
                imgSrc: img1
            },
            opinionTitle: " ",
            image: img1,
            opinionText: "                  ",
            score: 2,
            status: "  ",
            suggestion: "dislike"
        },
        {
            id: 2,
            product: {
                id: 0,
                title: "              ",
                imgSrc: img1
            },
            opinionTitle: " ",
            image: img1,
            opinionText: "                  ",
            score: 3,
            status: "  ",
            suggestion: "question"
        },
        {
            id: 3,
            product: {
                id: 0,
                title: "              ",
                imgSrc: img1
            },
            opinionTitle: " ",
            image: img1,
            opinionText: "                  ",
            score: 4,
            status: "  ",
            suggestion: "question"
        }
    ]);

    const opinionsContent = () => {
        if (opinions.length === 0 || opinions === null) {
            return  <Empty
                        title="   "
                        width="100%"
                        height="400px"
                    />
        } else {
            return (
                opinions.map( (item) => {
                    return (
                        <RegisteredOpinionItem
                            item={item}
                            key={item.id}
                        />
                    );
                })
            )
        }
    }
    
    return (
        <Wrapper>
            {opinionsContent()}

            {
                showEditOpinionModal === true
                    ?
                    <ProfileSubmitOpinionModal
                        type="edit"
                    />
                    :
                    null
            }

            {
                showDeleteOpinionModal === true
                    ?
                    <DeleteOpinionModal />
                    :
                    null
            }
        </Wrapper>
    );
}

export default RegisteredOpinions;