import * as Yup from 'yup';

export const ProfileSubmitOpinionFormSchema = Yup.object().shape({
    opinionTitle: Yup.string()
        .min(3,'     ')
        .required('     '),
    opinionText: Yup.string()
        .required('     '),
    activeIcon: Yup.string()
        .required('     '),
    ratingValue: Yup.number()
        .required('     '),
});